.switch {
    position: relative;
    display: inline-block;
    width: 3.2em;
    height: 1.4em;
    
    /* background-color: #fff; */
    margin-bottom: 0px;
    margin-right: 10px;
  }
  
  
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #fff;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 80%;
    width: 35%;
    left: 4px;
    background-color: #444444;
    bottom: 2px;
  
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #444444;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #4b4b4b57;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(20px);
    transform: translateX(20px);
    background: #fff;
  }
  

  .slider.round {
    border-radius: 34px;
    border: 1px solid #444444;
    
  }
  
  .slider.round:before {
    border-radius: 50%;
  }
  

